.sidebar-container {
    height: 100%;
    min-width: 355px;
    background-color: #fafafa;
}

h1 {
    color: black;
    -webkit-text-fill-color: #fafafa;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    font-family: 'Bebas Neue', sans-serif;
    text-align: center;
    font-size: 60px;
    border: 1px solid;
    padding-top: 5px;
    transition: all 5s ease;
    font-size: 50px;
    padding-left: 10px;
    padding-right: 10px;

}

h1:hover {
     -webkit-text-fill-color: black;
     -webkit-text-stroke-width: 1px;
     -webkit-text-stroke-color: #fafafa;
}

.about-container {
    text-align: center;
    border: 1px solid;
    width: fit-content;
    padding: 8px;
    margin: 15px;
    transition: all 0.3s ease-in-out;
}

.about-container:hover {
    box-shadow: 3px 2px 2px rgba(0,0,0,0.55);
    transition: all 0.3s ease-in-out;
    transform: scale(1.01, 1.01);
    cursor: pointer;
}


.sidebar-inner-container {
    width: 170px;
    margin-top: 300px;
    margin-left: 146px;
    margin-right: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-animation: fadein 7s;

}

.socials-container {
    display: flex;
    flex-direction: row;
}

.github, .linkedin, .angellist {
    margin: 0 10px;
}

.fab {
    transition: all 0.5s;
}

.fab:hover {
    color: grey; 
  width: 120%;
  transition: all 0.5s;
  cursor: pointer;
}

.icon-link {
    color: black;
}



